<template>
    <div id="app">
        <div v-show="stateGame">
            <div class="grid grid-rows-4 grid-cols-9 sticky top-0 border-b-4 border-slate-500 select-none">
                <div class="row-span-2">
                    <button class="bg-emerald-300 rounded-none w-full h-full"
                        @click="playerOneVP++">+</button>
                </div>
                <div class="bg-slate-100 row-span-3 col-span-2 text-center font-mono text-5xl p-4">{{ playerOneScore }}</div>
                
                <div class="bg-slate-500 text-slate-50 row-span-3 col-span-3 text-center font-mono text-4xl p-5">
                    <div class="">{{ timerText }}
                        <div class="float-right text-slate-200" @click="toggleTimer()"><i class="fas" :class="timerIcon"></i></div>
                        <div class="float-left text-slate-200" @click="resetTimer()"><i class="fas fa-refresh"></i></div>
                    </div>
                </div>
            
                <div class="bg-slate-100 row-span-3 col-span-2 text-center font-mono text-5xl p-4">{{ playerTwoScore }}</div>   
                
                <div class="row-span-2">
                    <button class="rounded-none w-full h-full bg-emerald-300"
                        @click="playerTwoVP++">+</button>
                </div>
                
                



                

                <div class="row-span-2 ">
                    <button class="rounded-none w-full h-full bg-rose-300"
                        @click="playerOneVP > 0 ? playerOneVP-- : 0">-</button>
                </div>
                

                <div class="row-span-2 ">
                    <button class="rounded-none w-full h-full bg-rose-300"
                        @click="playerTwoVP > 0 ? playerTwoVP-- : 0">-</button>
                </div>

                <div class="text-center font-mono text-sm bg-slate-300 p-1">OP: {{ playerOneVP }}</div>
                <div class="text-center font-mono text-sm bg-slate-200 p-1">SP: {{ playerOneSP }}</div>
                
                <div class="">
                    <button class="bg-rose-300 rounded-none w-full h-full"
                        @click="roundNumber > 1 ? roundNumber-- : 0">-</button>
                </div>
                <div class="text-center font-mono bg-slate-100">Round {{ roundNumber }}</div>
                <div class="">
                    <button class="bg-emerald-300 rounded-none w-full h-full"
                        @click="roundNumber++">+</button>
                </div>
                <div class="text-center font-mono text-sm bg-slate-200 p-1">SP: {{ playerTwoSP }}</div>   
                <div class="text-center font-mono text-sm bg-slate-300 p-1">OP: {{ playerTwoVP }}</div>
                
            </div>

            <div class="grid grid-cols-2 select-none">
                <div>
                    <ul v-for="ship in playerOneList" :key="ship.id">
                        <li class="rounded-none border-2 border-slate-400 m-2 p-4" :class="shipStateClass(ship)" @click="cycleShipState(ship)">
                            <div class="">
                                {{ ship.name }} 
                                <div class="float-right">
                                    {{ ship.points }}/{{ Math.floor(ship.points / 2) }}
                                </div>
                            </div>
                        </li>
                    </ul>
                </div>

                <div>
                    <ul v-for="ship in playerTwoList" :key="ship.id">
                        <li class="rounded-none border-2 border-slate-400 m-2 p-4" :class="shipStateClass(ship)" @click="cycleShipState(ship)">
                            <div class="">
                                {{ ship.name }} 
                                <div class="float-right">
                                    {{ ship.points }}/{{ Math.floor(ship.points / 2) }}
                                </div>
                            </div> 
                        </li>
                    </ul>
                </div>
            </div>
            <div class="fixed bottom-0 w-full text-center">
                <div class="rounded-none bg-rose-300 p-2" @click="endGame()">End Game</div>
            </div>
        </div>
        <div v-show="stateBuild">
            <div class="text-center">
                <h3>Player One: {{playerOneListPoints}}</h3>
                <ul v-for="ship in playerOneList" :key="ship.id">
                    <li class="rounded-none border-2 border-slate-400 m-2 p-4">
                        {{ship.name}}
                        <div class="float-right" @click="deleteShip(ship)"><i class="fas fa-trash"></i> </div>
                        <div class="float-right mr-10">{{ship.points}}</div>
                    </li>
                </ul>
                <div class="rounded-none border-2 border-slate-500 m-2 p-4" v-if="stateAddingFirst">
                    <input type="text" v-model="ship.name" class="p-3 border-2 border-slate-300" placeholder="Pilot Name">
                    <input type="number" v-model="ship.points" class="p-3 border-2 border-slate-300" placeholder="Points"> 
                    <button class="border-2 border-red-300 bg-red-100 p-3" @click="stateAddingFirst = false"><i class="fa fa-cancel" ></i></button>
                    <button class="border-2 border-green-300 bg-green-100 p-3" @click="addShip(1)"><i class="fa fa-check"></i></button>
                    
                </div>
                <div class="rounded-none border-2 border-green-500 bg-green-200 m-2 p-4" @click="stateAddingFirst = true">Add Ship</div>

                <h3 class="mt-20">Player Two: {{playerTwoListPoints}}</h3>
                <ul v-for="ship in playerTwoList" :key="ship.id">
                    <li class="rounded-none border-2 border-slate-400 m-2 p-4">
                        {{ship.name}}
                        <div class="float-right" @click="deleteShip(ship)"><i class="fas fa-trash"></i> </div>
                        <div class="float-right mr-10">{{ship.points}}</div>
                    </li>
                </ul>
                <div class="rounded-none border-2 border-slate-500 m-2 p-4" v-if="stateAddingSecond">
                    <input type="text" v-model="ship.name" class="p-3 border-2 border-slate-300" placeholder="Pilot Name">
                    <input type="number" v-model="ship.points" class="p-3 border-2 border-slate-300" placeholder="Points"> 
                    <button class="border-2 border-red-300 bg-red-100 p-3" @click="stateAddingSecond = false"><i class="fa fa-cancel" ></i></button>
                    <button class="border-2 border-green-300 bg-green-100 p-3" @click="addShip(2)"><i class="fa fa-check"></i></button>
                </div>
                <div class="rounded-none border-2 border-green-500 bg-green-200 m-2 p-4" @click="stateAddingSecond = true">Add Ship</div>

                <div class="rounded-none border-2 border-red-500 bg-red-200 m-2 p-4 mt-20" @click="startGame()">Start Game</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    setup() {
        
    },
    data() {
        return {
            ships: [
            ],


            ship: {
                id: null,
                player: 0,
                name: '',
                points: '',
                state: '',
            },
            maxId: 0,

            
            playerOneVP: 0,
            playerTwoVP: 0,

            roundNumber: 1,

            timerRunning: false,
            timerRemaining: 4500000, //75 minutes
            timerEnd: null,
            timer: undefined,
            timeNow: null,
            timerText: "75:00",
            stateGame: false,
            stateBuild: true,
            stateAddingFirst: false,
            stateAddingSecond: false,

        };
    },
    computed: {
        playerOneScore() {
            return this.playerOneSP + this.playerOneVP;
        },
        playerTwoScore() {
            return this.playerTwoSP + this.playerTwoVP;
        },
        playerOneList() {
            return this.ships.filter(ship => ship.player === 1);
        },
        playerTwoList() {
            return this.ships.filter(ship => ship.player === 2);
        },
        playerOneSP() {
            let points = 0;
            for(let ship of this.playerTwoList)
            {
                if( ship.state === "destroyed" ) { points += ship.points; }
                else if (ship.state === "half" ) { points += Math.floor(ship.points/2); }
            }
            return points;
        },

        playerOneListPoints() {
            let points = 0;
            for(let ship of this.playerOneList)
            {
                points += ship.points;
            }
            return points;
        },
        playerTwoListPoints() {
            let points = 0;
            for(let ship of this.playerTwoList)
            {
                points += ship.points;
            }
            return points;
        },

        playerTwoSP() {
            let points = 0;
            for(let ship of this.playerOneList)
            {
                if( ship.state === "destroyed" ) { points += ship.points; }
                else if (ship.state === "half" ) { points += Math.floor(ship.points/2); }
            }
            return points;
        },

        timerIcon() {
            if(this.timerRunning){
                return 'fa-pause';
            }
            else {
                return 'fa-play';
            }
        },
        
    },
    methods: {
        cycleShipState(ship) {
            this.ship = Object.assign({}, ship);
            if(this.ship.state === "full") {
                this.ship.state = "half";
            }
            else if (this.ship.state === "half") {
                this.ship.state = "destroyed";
            }
            else {
                this.ship.state = "full";
            }

            let index = this.ships.findIndex(ship => ship.id === this.ship.id);

            this.ships.splice(index, 1, this.ship);
        },
        shipStateClass(ship) {
            if( ship.state === "full" ) { return "bg-slate-100"; }
            else if (ship.state === "half") { return "bg-yellow-100"; }
            else { return "bg-red-300"; }
        },
        deleteShip(ship) {
            this.ship = Object.assign({}, ship);
            let index = this.ships.findIndex(ship => ship.id === this.ship.id);
            this.ships.splice(index, 1);
        },
        addShip(player) {
            this.ship.id = this.maxId;
            this.maxId++;
            this.ship.player = player;
            this.ship.state = "full";

            let newShip = Object.assign({}, this.ship);

            this.ships.push(newShip);

            this.stateAddingFirst = false;
            this.stateAddingSecond = false;

        },
        toggleTimer(){
            this.timerRunning = !this.timerRunning;

            if(this.timerRunning) {
                // record end time
                this.timerEnd = Date.now() + this.timerRemaining;
                this.timer = setInterval(this.updateTimer, 500);
            }
            else {
                clearInterval(this.timer);
            }
        },
        resetTimer() {
            this.timerRunning = false;
            clearInterval(this.timer);
            this.timerRemaining = 4500000;
            this.timerText = "75:00";

        },
        updateTimer(){
            if(this.timerRunning){
                this.timerRemaining = this.timerEnd - Date.now();
            }
 
            let minutes = Math.floor(this.timerRemaining / (1000 * 60));
            let seconds = Math.floor((this.timerRemaining - (minutes * 60 * 1000))/1000);
            this.timerText = minutes + ":" + String(seconds).padStart(2, '0');
        },
        endGame(){
            clearInterval(this.timer);
            this.stateGame = false;
            this.stateBuild = true;
            if(this.timerRunning) {
                this.toggleTimer();
            }
        },
        startGame(){
            this.stateGame = true;
            this.stateBuild = false;

            for(let ship of this.ships) {
                ship.state = "full";
            }
            this.playerOneVP = 0;
            this.playerTwoVP = 0;
            this.resetTimer();
            
        }
    },
}
</script>
